:root{--range-handle-border:transparent}.rangeSlider{@apply h-0.5 bg-gray-200 m-0;}.rangeSlider .rangeBar{@apply h-0.5 bg-gray-900;}.rangeSlider.focus .rangeBar{@apply h-0.5 bg-gray-900;}.rangeSlider .rangeHandle .rangeNub, .rangeSlider .rangeHandle.active .rangeNub{@apply bg-white border-2 border-gray-900 rounded-full -mt-1;}.rangeSlider .rangeHandle.hoverable .rangeNub:hover{@apply border-8 bg-gray-100;}.rangeSlider .rangeHandle.press .rangeNub {@apply border-8 bg-gray-200;}